.wp-block-cards-block-list {
  min-height: 5rem;
}
.wp-block-cards-block-list .cards-list-search {
  margin: 0.3em 0.3em 1.5rem;
}
.wp-block-cards-block-list .cards-list-search.is-hidden {
  display: none;
}
.wp-block-cards-block-list .cards-list-search .cards-list-search-controls .icon {
  display: inline-block;
  background: #fff;
  border-radius: 1rem;
  width: 2rem;
  height: 2rem;
  line-height: 2;
  vertical-align: middle;
  text-align: center;
}
.wp-block-cards-block-list .cards-list-search .cards-list-search-controls .icon svg {
  width: 1.5rem;
  height: 1.5rem;
}
.wp-block-cards-block-list .cards-list-search .cards-list-search-results {
  position: absolute;
  list-style: none;
  padding-left: 0;
  z-index: 5;
  display: none;
}
.wp-block-cards-block-list .cards-list-search .cards-list-search-results.is-visible {
  display: block;
}
.wp-block-cards-block-list .cards-list-search .cards-list-search-results > li {
  margin: 0 auto;
}
.wp-block-cards-block-list .cards-list-search .cards-list-search-results .search-result {
  margin: 0;
  display: block;
  position: relative;
}
.wp-block-cards-block-list .cards-list-search .cards-list-search-results .search-result figcaption {
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateY(5px);
  z-index: 6;
  background-color: #074365;
  padding: 0.1rem 0.3rem;
  font-size: 0.6rem;
  margin: 0;
}
.wp-block-cards-block-list .cards-list-search .cards-list-search-results .search-result figcaption a {
  color: #fff;
}